.home-op {
  background-color: var(--dark-2);
  width: 100%;
  height: 180px;
  margin-bottom: 2rem;
  margin-top: -20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  font-weight: 900;
  position: relative;
  overflow: hidden;

  clip-path: polygon(100% 0, 100% 60%, 20% 100%, 0 60%, 0 0);
}
.home-op .title {
  margin-top: -40px;
  text-align: center;
}
.home-op::before {
  background-color: var(--primary);
  position: absolute;
  content: "";
  width: 23%;
  height: 200%;
  top: 0;
  left: 0;
  z-index: -1;
  transform: rotateZ(25deg) scale(3);
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.486);
}
