.about-con {
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
  /* height: 30vh; */
  color: white;
}
.about-con .img-con {
  /* background: blue; */
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  overflow: hidden;
}
.about-con img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-con .info-con {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
}
/* .about-con .info {
  margin: 0 auto;
} */

.about-con .info ul {
  margin-left: 1rem;
  text-align: left;
}

.about-con .info ul li {
  list-style-type: square;
}

@media (max-width: 600px) {
  .about-con {
    flex-wrap: wrap;
  }
  .about-con .img-con {
    width: 100%;
    margin-bottom: 2rem;
  }

  .about-con .info-con {
    text-align: center;
    width: 100%;
  }
  .about-con .info ul {
    margin-left: 0;
    text-align: left;
    padding-left: 0;
  }

  .about-con .info ul li {
    text-align: center;
    list-style-type: none;
  }
}
