:root {
  --primary: #ee1e46;
  --dark: #222831;
  --dark-2: #1a1e25;

  --blue: #007bff;
  --indigo: #0d0cb5;
  --purple: #7971ea;
  --pink: #e83e8c;
  --red: #f23a2e;
  --orange: #ef6c57;
  --yellow: #f89d13;
  --green: #8bc34a;
  --teal: #48d494;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  /* --primary: #ee1e46; */
  --secondary: #6c757d;
  --success: #8bc34a;
  --info: #17a2b8;
  --warning: #f89d13;
  --danger: #f23a2e;
  --light: #f8f9fa;
  /* --dark: #343a40; */
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --font-family-sans-serif: "Montserrat", -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
}

.top-con {
  background-color: var(--dark);
  /* background: url("https://preview.colorlib.com/theme/soccer/images/xbg_3.jpg.pagespeed.ic.Dxj9GRLtTU.webp"); */
  /* background: url("https://cdna.artstation.com/p/assets/images/images/022/504/744/original/enrcio-schoebben-volleyball-clip-1-gif.gif?1575655730"); */
  height: 93vh;
  min-height: 500px;
  background-position: center;
  background-size: cover;
  position: relative;
}
/* @media (max-width: 600px) {
  .top-con {
    height: 95vh;
  }
} */
.top-con:before {
  content: "";
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* z-index: -1; */
}

section {
  min-height: 200px;
  position: relative;
  padding: 2rem;

  /* margin-bottom: 6rem; */
}
section:nth-child(2n -1) {
  background-color: var(--dark-2);
}
.section-inner {
  /* padding: 2rem; */
}

section h2 {
  color: white;
  margin: 0 0 1.5rem 0;
  border-left: 8px solid var(--primary);
  /* margin-left: 1rem; */
}
