.tournaments-2021 {
  background-color: var(--dark-2);
  width: 100%;
  height: 180px;
  margin-bottom: 2rem;

  clip-path: polygon(50% 0, 100% 39%, 100% 99%, 0 99%, 0% 38%);

  margin-top: -120px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  font-weight: 900;
  position: relative;
  overflow: hidden;
}
.tournaments-2021 .title {
  margin-bottom: -30px;
  text-align: center;
}
.tournaments-2021::before {
  background-color: var(--primary);
  position: absolute;
  content: "";
  width: 30%;
  height: 200%;
  top: 0;
  left: 0;
  z-index: -1;
  transform: rotateZ(20deg) scale(2);
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.486);
}
@media (max-width: 480px) {
  .tournaments-2021 {
    height: 130px;
    font-size: 1.5rem;
  }

  .tournaments-2021::before {
    width: 30%;
    height: 100%;
  }
}
