@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Montserrat", sans-serif;
  color: var(--primary);
  background: var(--dark);
  /* background: url("https://preview.colorlib.com/theme/soccer/images/xbg_3.jpg.pagespeed.ic.Dxj9GRLtTU.webp"); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
}

:root {
  --primary: #ee1e46;
  --dark: #222831;
  --dark-2: #1a1e25;

  --blue: #007bff;
  --indigo: #0d0cb5;
  --purple: #7971ea;
  --pink: #e83e8c;
  --red: #f23a2e;
  --orange: #ef6c57;
  --yellow: #f89d13;
  --green: #8bc34a;
  --teal: #48d494;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  /* --primary: #ee1e46; */
  --secondary: #6c757d;
  --success: #8bc34a;
  --info: #17a2b8;
  --warning: #f89d13;
  --danger: #f23a2e;
  --light: #f8f9fa;
  /* --dark: #343a40; */
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --font-family-sans-serif: "Montserrat", -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
}

.top-con {
  background-color: #222831;
  background-color: var(--dark);
  /* background: url("https://preview.colorlib.com/theme/soccer/images/xbg_3.jpg.pagespeed.ic.Dxj9GRLtTU.webp"); */
  /* background: url("https://cdna.artstation.com/p/assets/images/images/022/504/744/original/enrcio-schoebben-volleyball-clip-1-gif.gif?1575655730"); */
  height: 93vh;
  min-height: 500px;
  background-position: center;
  background-size: cover;
  position: relative;
}
/* @media (max-width: 600px) {
  .top-con {
    height: 95vh;
  }
} */
.top-con:before {
  content: "";
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* z-index: -1; */
}

section {
  min-height: 200px;
  position: relative;
  padding: 2rem;

  /* margin-bottom: 6rem; */
}
section:nth-child(2n -1) {
  background-color: #1a1e25;
  background-color: var(--dark-2);
}
.section-inner {
  /* padding: 2rem; */
}

section h2 {
  color: white;
  margin: 0 0 1.5rem 0;
  border-left: 8px solid #ee1e46;
  border-left: 8px solid var(--primary);
  /* margin-left: 1rem; */
}

.header-con {
  /* background-color: rgb(185, 185, 255); */
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem 0;
  position: relative;
  z-index: 1;
}
.logo {
  font-weight: 900;
  font-size: 2rem;
  color: white;
}
.menu {
  display: flex;
}
ul {
  margin: 0;
}

.menu a {
  padding: 5px 20px;
}
.menu li {
  font-size: 12px;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  font-weight: 400;
  display: inline-block;
  text-decoration: none !important;
  /* color: white; */
  padding: 4px 0;
}

@media (max-width: 991px) {
  .phn-menu-btn {
    display: block !important;
  }

  .menu {
    display: none;
  }
}
.phn-menu-btn {
  display: none;
  cursor: pointer;
  color: white;
  font-size: 1.5rem;
}

.phn-menu-con {
  background-color: var(--dark-2);
  height: 100vh;
  width: 0px;
  position: fixed;
  top: 0;
  right: 0;
  transition: 0.3s;
}
.phn-menu .link {
  color: white;
}

.phn-menu .link:hover {
  color: var(--primary);
}
.close-menu-btn {
  cursor: pointer;
  width: 100%;
  padding-left: 1.5rem;
  font-weight: 500;
  font-size: 1.5rem;
  height: 96px;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

/* .phn-menu ul {
  background: green;
} */
.phn-menu ul li {
  padding: 8px;
}

.phn-menu i {
  margin-right: 12px;
}
/* Active classes  */
.active-menu {
  color: var(--primary);
}
.d-menu {
  color: white;
}
.d-menu:hover {
  color: rgb(255, 255, 255);
  opacity: 0.7;
}
.d-menu-active {
  color: white;
  border-bottom: 2px solid white !important;
}

.about-con {
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
  /* height: 30vh; */
  color: white;
}
.about-con .img-con {
  /* background: blue; */
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  overflow: hidden;
}
.about-con img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-con .info-con {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
}
/* .about-con .info {
  margin: 0 auto;
} */

.about-con .info ul {
  margin-left: 1rem;
  text-align: left;
}

.about-con .info ul li {
  list-style-type: square;
}

@media (max-width: 600px) {
  .about-con {
    flex-wrap: wrap;
  }
  .about-con .img-con {
    width: 100%;
    margin-bottom: 2rem;
  }

  .about-con .info-con {
    text-align: center;
    width: 100%;
  }
  .about-con .info ul {
    margin-left: 0;
    text-align: left;
    padding-left: 0;
  }

  .about-con .info ul li {
    text-align: center;
    list-style-type: none;
  }
}

.hero-con {
  height: calc(100% - 96px);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-top: -30px;
  color: white;
}
.hero-inner {
  text-align: center;
  padding: 0 10%;
}
.hero-con h1 {
  font-weight: 700;
  font-size: 3rem;
}
@media (max-width: 480px) {
  .hero-con h1 {
    font-size: 2rem;
  }
}
/* Home  */
.join-now-btn-con {
  /* background-color: blue; */
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 15%;
  left: calc(50% - 54px);
}
.join-now-btn {
  border: none;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 12px;
  margin-top: 100px;
  background-color: var(--primary);
  color: white;
  width: 108px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.join-now-btn:hover {
  background-color: transparent;
  color: var(--primary);
  /* border: 2px solid var(--primary); */
}

.slide-1 img {
  height: 300px;
  width: 100%;
  object-fit: contain;
}
/* .button__bar li.slick-active button {
  opacity: 0.75;
  color: red;
} */
@media (min-width: 700px) {
  .slide-1 img {
    height: 600px;
  }
}

.slide-2 img {
  height: 100px;
  /* min-height: 100px; */
  width: 100%;
  object-fit: cover;
}
@media (min-width: 700px) {
  .slide-2 img {
    height: 300px;
  }
}

