} */

.tournaments-2021 {
  background-color: var(--dark-2);
  width: 100%;
  height: 180px;
  margin-bottom: 2rem;

  -webkit-clip-path: polygon(50% 0, 100% 39%, 100% 99%, 0 99%, 0% 38%);

          clip-path: polygon(50% 0, 100% 39%, 100% 99%, 0 99%, 0% 38%);

  margin-top: -120px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  font-weight: 900;
  position: relative;
  overflow: hidden;
}
.tournaments-2021 .title {
  margin-bottom: -30px;
  text-align: center;
}
.tournaments-2021::before {
  background-color: var(--primary);
  position: absolute;
  content: "";
  width: 30%;
  height: 200%;
  top: 0;
  left: 0;
  z-index: -1;
  transform: rotateZ(20deg) scale(2);
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.486);
}
@media (max-width: 480px) {
  .tournaments-2021 {
    height: 130px;
    font-size: 1.5rem;
  }

  .tournaments-2021::before {
    width: 30%;
    height: 100%;
  }
}


.contact-form-con {
  display: flex;
  flex-wrap: wrap;
}
.contact-form-con form {
  width: 60%;
}

.contact-form-con form input,
textarea {
  width: 100%;
  height: 43px;
  border-radius: 4px;
  background: none;
  border-width: 1px;
  color: #fff;
  border-color: rgba(255, 255, 255, 0.2);
  padding: 0 1rem;
  margin-bottom: 1rem;
  transition: 0.2s;
}
.contact-form-con form input:focus,
textarea:focus {
  border: 1px solid var(--primary) !important;
  outline: none !important;
}
textarea {
  min-height: 200px;
  padding: 0.8rem !important;
}
.send-btn {
  background-color: var(--primary) !important;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  height: auto !important;
  padding: 0.7rem 2rem !important;
}
.send-btn:hover {
  background-color: transparent !important;
}
.contact-form-con .contact-address {
  width: 40%;
  padding-left: 10%;
  color: white;
}
.primary-color {
  color: var(--primary);
}
/* Responsiveness code  */
@media (max-width: 991px) {
  .contact-form-con form {
    width: 100%;
    margin-bottom: 2rem;
  }

  .contact-form-con .contact-address {
    width: 100%;
    padding-left: 0;
  }
}


.footer-con {
  text-align: center;
  padding: 2rem 0 1rem 0;
}
.footer-con .copyright {
  font-size: 0.8rem;
  font-weight: 600;
}
.darkroom {
  text-transform: uppercase;
  font-size: 0.4rem;
  margin-top: 1rem;
}

.darkroom span {
  color: white;
  font-size: 0.6rem;
}

