.contact-form-con {
  display: flex;
  flex-wrap: wrap;
}
.contact-form-con form {
  width: 60%;
}

.contact-form-con form input,
textarea {
  width: 100%;
  height: 43px;
  border-radius: 4px;
  background: none;
  border-width: 1px;
  color: #fff;
  border-color: rgba(255, 255, 255, 0.2);
  padding: 0 1rem;
  margin-bottom: 1rem;
  transition: 0.2s;
}
.contact-form-con form input:focus,
textarea:focus {
  border: 1px solid var(--primary) !important;
  outline: none !important;
}
textarea {
  min-height: 200px;
  padding: 0.8rem !important;
}
.send-btn {
  background-color: var(--primary) !important;
  width: fit-content !important;
  height: auto !important;
  padding: 0.7rem 2rem !important;
}
.send-btn:hover {
  background-color: transparent !important;
}
.contact-form-con .contact-address {
  width: 40%;
  padding-left: 10%;
  color: white;
}
.primary-color {
  color: var(--primary);
}
/* Responsiveness code  */
@media (max-width: 991px) {
  .contact-form-con form {
    width: 100%;
    margin-bottom: 2rem;
  }

  .contact-form-con .contact-address {
    width: 100%;
    padding-left: 0;
  }
}
