.header-con {
  /* background-color: rgb(185, 185, 255); */
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem 0;
  position: relative;
  z-index: 1;
}
.logo {
  font-weight: 900;
  font-size: 2rem;
  color: white;
}
.menu {
  display: flex;
}
ul {
  margin: 0;
}

.menu a {
  padding: 5px 20px;
}
.menu li {
  font-size: 12px;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  font-weight: 400;
  display: inline-block;
  text-decoration: none !important;
  /* color: white; */
  padding: 4px 0;
}

@media (max-width: 991px) {
  .phn-menu-btn {
    display: block !important;
  }

  .menu {
    display: none;
  }
}
.phn-menu-btn {
  display: none;
  cursor: pointer;
  color: white;
  font-size: 1.5rem;
}

.phn-menu-con {
  background-color: var(--dark-2);
  height: 100vh;
  width: 0px;
  position: fixed;
  top: 0;
  right: 0;
  transition: 0.3s;
}
.phn-menu .link {
  color: white;
}

.phn-menu .link:hover {
  color: var(--primary);
}
.close-menu-btn {
  cursor: pointer;
  width: 100%;
  padding-left: 1.5rem;
  font-weight: 500;
  font-size: 1.5rem;
  height: 96px;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

/* .phn-menu ul {
  background: green;
} */
.phn-menu ul li {
  padding: 8px;
}

.phn-menu i {
  margin-right: 12px;
}
/* Active classes  */
.active-menu {
  color: var(--primary);
}
.d-menu {
  color: white;
}
.d-menu:hover {
  color: rgb(255, 255, 255);
  opacity: 0.7;
}
.d-menu-active {
  color: white;
  border-bottom: 2px solid white !important;
}
