.hero-con {
  height: calc(100% - 96px);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-top: -30px;
  color: white;
}
.hero-inner {
  text-align: center;
  padding: 0 10%;
}
.hero-con h1 {
  font-weight: 700;
  font-size: 3rem;
}
@media (max-width: 480px) {
  .hero-con h1 {
    font-size: 2rem;
  }
}
/* Home  */
.join-now-btn-con {
  /* background-color: blue; */
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 15%;
  left: calc(50% - 54px);
}
.join-now-btn {
  border: none;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 12px;
  margin-top: 100px;
  background-color: var(--primary);
  color: white;
  width: 108px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.join-now-btn:hover {
  background-color: transparent;
  color: var(--primary);
  /* border: 2px solid var(--primary); */
}
